.container {
    max-width: 400px;
    header {
        background-color: #1C313A;
        width: 100%;
        max-width: 400px;
        top: 0;
        height: 4.5rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: fixed;
        .headerProfileHolder {
            display: flex;
            align-items: center;
            margin-left: 2rem;
            img {
                border-radius: 50%;
                border: 0.1rem solid white;
                height: 2.5rem;
            }
            .headerNameHolder {
                margin-left: 0.5rem;
                h4 {
                    font-size: 0.75rem;
                    color: white;
                    font-weight: 400;
                    margin: 0;
                    padding: 0;
                }
                p {
                    color: white;
                    margin: 0;
                    padding: 0;
                    font-weight: 200;
                    font-size: 0.75rem;
                }
            }
        }
        .menuIcon {
            color: white;
            font-size: 1.5rem;
            margin-right: 1.5rem;
            z-index: 99;
        }
        .menuOpen {
            position: absolute;
            top: 1rem;
            right: 1rem;
            background-color: #424242;
            width: 13rem;
            z-index: 1;
            h3 {
                color: white;
                font-size: 1rem;
                font-weight: 400;
                margin: 0.5rem 0 0 1rem;
            }
            ul {
                list-style-type: none;
                margin: 0;
                padding: 0;
                width: 100%;
                display: flex;
                align-items: center;
                flex-direction: column;
                margin: 1rem 0;
                li {
                    color: white;
                    font-size: 1rem;
                    font-weight: 400;
                    margin: 0.5rem 0;
                }
                .link {
                    text-decoration: none;
                }
            }
        }
    }
}